.contactsBlock{
    height: 100vh;
    background-color: #a07575;
}
.contactsContainer{
    display: flex;
    flex-direction: column;
    justify-content: center;

}
.contacts {
    justify-content: center;
    border: 1px solid;
    width: 80%;
    height: 60%;
    display: flex;

    flex-wrap: wrap;
    text-align: center;
    align-items: center;
    flex-direction: column;
}
.form{
    width: 100%;

    background-color: rgb(252, 243, 248);
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    border: 1px solid;
    margin-bottom: 20px;

}
@media screen and (min-width: 875px){
    .form{
        width: 320px;
    }
}